
import { store } from '@/store/store';
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'TheUploadEncryptedFiles.vue',

  data() {
    return {
      files: [] as any,
    };
  },

  methods: {
    handleFileChange() {
      // @ts-ignore
      this.files = this.$refs.fileInput.files;
      store.files = this.files;
    },
  },
});
